body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.container {
  width: 100vw;
  height: 100vh;
}

.content {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
}

.element {
  width: 500px;
  max-width: 70vw;
  border: 2px solid rgba(90, 90, 90, 0.5);
}

.element img {
  width: 100%;
}

.element {
  margin: auto;
}

.tools {
  position: absolute;
  top: 10px;
  right: 10px;
  display: grid;
  grid-gap: 10px;
}



